import React from 'react'
import { observer } from 'mobx-react'

import styles from './index.scss'
import { useOnMount } from '@utils/hooks'
import useRootStore from '@store/useRootStore'
import Masonry from 'react-masonry-css'

interface IProps {}

const breakpointColumnsObj = {
    default: 4,
    1100: 3,
    700: 2,
    500: 1
}

function StaffTable({}: IProps) {
    const { staffStore } = useRootStore()

    useOnMount(staffStore.getStaff)

    return (
        <Masonry
            breakpointCols={breakpointColumnsObj}
            className={styles['masonry-grid']}
            columnClassName={styles['masonry-grid_column']}
        >
            {staffStore.staff.map(item => (
                <div key={item._id}>
                    <img
                        src={item.img}
                        style={{
                            width: '100%'
                        }}
                    />
                    <div className={styles.staffText}>
                        <div className={styles.nameAndTitle}>
                            <div className={styles.name}>{item.name}</div>
                            <div className={styles.title}>{item.title}</div>
                        </div>
                        <div className={styles.description}>{item.description}</div>
                        <div className={styles.jobHistory}>{item.jobHistory}</div>
                        <div className={styles.hireDate}>Joined: {item.hireDate}</div>
                    </div>
                </div>
            ))}
        </Masonry>
    )
}

export default observer(StaffTable)
